.tYBbsG_buttons {
  gap: 4px;
  margin-bottom: 6px;
  display: flex;
}

.tYBbsG_buttons button {
  color: var(--color-white);
  background-color: var(--color-black);
  border: none;
  padding: 6px 10px;
  transition: background-color .2s ease-in-out, color .2s ease-in-out;
}

.tYBbsG_buttons button:not(.tYBbsG_active):hover {
  background-color: var(--color-white-fade);
}

.tYBbsG_buttons .tYBbsG_active {
  color: var(--color-black);
  border-color: var(--color-white);
  background-color: var(--color-white);
}

.tYBbsG_content {
  border-radius: var(--radius);
  background-color: var(--color-black);
  border: 1px solid var(--color-grey-100);
  min-height: 200px;
  padding: 12px;
}

.tYBbsG_content p:first-of-type {
  margin-top: 0;
}

.tYBbsG_content p:last-of-type {
  margin-bottom: 0;
}

